.contacts {
    padding-top: 75px;
    &__title {
        margin: 0;
        margin-bottom: 33px;
        text-align: center;
        font-size: 50px;
        font-weight: 300;
        color: $light-grey;
        @include md-block() {
            font-size: 40px;
            font-weight: 300;
            margin-bottom: 30px; }
        @include sm-block() {
            font-size: 30px; } } }

.contacts-block__wrapper {
    @include row-flex();
    justify-content: space-around;
    @include sm-block() {
        justify-content: center; } }

.contacts-block__wrapper .contacts-block:nth-child(1) {
    @include md-block() {
        order: -1; } }

.contacts-block__wrapper .contacts-block:nth-child(3) {
    @include md-block() {
        order: -1; } }

.contacts-block {
    @include col();
    text-align: center;
    &__title {
        font-size: 18px;
        font-weight: 700;
        color: $light-grey; }
    &__description {
        font-size: 24px;
        font-weight: 300;
        color: #d22856; }
    &__map-wrapper {
        position: relative;
        min-width: 100%;
        min-height: 574px;
        background-image: url(../img/All/map.png);
        background-position: center center;
        background-repeat: no-repeat;
        @include md-block() {
            min-height: 377px; } }
    &__map {
        width: 100%;
        height: auto; } }

